// Global Imports

// @import 'base/index';
// @import 'layout/index';
// @import 'components/index';

$color-black: #000;
$color-eerieblack: #252525;
$color-white: #fff;
$color-leatherjacket: #253825;
$color-cyanaqua: #15ffff;
$color-darkgray: #404040;
$color-canary: #1515FF;
$color-igpurple: #8a3ab9;
$color-igredorange: #e95950;
$color-igredviolet: #bc2a8d;
$color-igyellow: #fccc63;
$color-igorange: #fbad50;
$color-igmaroon: #cd486b;
$color-igblue: #4c68d7;
$color-fbblue: #3b5998;

$font-nunito: 'Nunito Sans', sans-serif;
$font-rubik: 'Rubik', sans-serif;
$font-montserrat: 'Montserrat', sans-serif;

html,
body {
    height: 100%;
    background-color: $color-black;
    scroll-behavior: smooth;
}

// Global fonts

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $font-montserrat;
}

p,
span {
    font-family: $font-nunito;
    font-size: 1.125rem;
    color: $color-white;
    font-weight: 400;
}

.drk:after {
    content:"";
    display:block;
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background: $color-black;
    opacity:0.2;
}

// header

.hr-1 {
    width: 50%;
    border-top: 1px solid $color-white;
}

.hr-2 {
    width: 50%;
    border-top: 1px solid $color-black;
}

.first-section h1 a {
    text-decoration: none;
    color: $color-cyanaqua;
}

.first-section h1 {
    font-style: normal;
    font-weight: 500;
    letter-spacing: .0625rem;

}

.first-section h5 {
    color: $color-white;
    font-style: normal;
    font-weight: 400;
    letter-spacing: 0.125rem;
}

// Navigation

.nav-link:hover {
    color: $color-canary !important;
}

.navbar-nav li {
    font-style: normal;
    font-weight: 400;
    font-size: 1.125rem;
    font-family: $font-rubik;
}

// Carousel

#carousel h3 {
    font-size: 3rem;
}

#carousel p {
    font-size: 1.25rem;
}

#carousel h3,
#carousel p {
    font-style: normal;
    font-weight: 400;
    letter-spacing: 0.125rem;
    text-shadow: .125rem .0625rem .25rem $color-darkgray;
}

// Content Headers

#media h1,
#about h1,
#code h1,
#contact h1 {
    color: $color-cyanaqua;
    letter-spacing: 0.25rem;
}

// Hobbies Section

#media-1 img,
#media-2 img,
#media-3 img,
#contact img {
    box-shadow: .3125rem .3125rem 1.25rem $color-leatherjacket;
}

// About Section

#about h3 {
    color: $color-white;
    letter-spacing: 0.25rem;
    font-weight: lighter;
    font-family: $font-rubik;
}

// Portfolio Page

#portfolio h1 {
    color: $color-cyanaqua;
    letter-spacing: 0.25rem;
}

.card p,
.card span {
    color: $color-black;
}

.card h4 {
    color: $color-canary;
}

.card a {
    color: $color-canary;
}

.card a:hover {
    text-decoration: none;
}


// Contact Section

#contact label,
.btn {
    color: $color-white;
    font-family: $font-rubik;
    letter-spacing: 0.125rem;
    font-weight: 400;
}

// Footer

#footer {
    background-color: $color-white;
}

#footer span {
    color: $color-black;
}

#social-media .fa-facebook,
#social-media .fa-facebook:visited,
#social-media .fa-instagram,
#social-media .fa-instagram:visited {
    color: $color-canary;
}

#social-media .fa-facebook:hover {
    color: $color-fbblue;
}

#social-media .fa-instagram:hover {
    background: conic-gradient(from -15deg, $color-igpurple, $color-igredorange, $color-igredviolet, $color-igyellow, $color-igorange, $color-igmaroon, $color-igblue);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    -webkit-text-fill-color: transparent;
}

#btn-back-to-top {
    position: fixed;
    bottom: 1.25rem;
    right: 1.25rem;
    display: none;
    transition: all 1s ease-in-out;
  }

#btn-back-to-top:hover {
    background-color: $color-cyanaqua;
    border-color: $color-cyanaqua;
    transform: rotate(180deg);
}